
import { NextRequest, NextResponse } from 'next/server';
import * as admin from 'firebase-admin';

// Initialize Firebase Admin SDK if not already initialized
if (admin.apps.length === 0) {
  admin.initializeApp();
}
const firestore = admin.firestore();

// Define secrets - these should be set as environment variables in your deployment
const RAZORPAY_KEY_ID = process.env.RAZORPAY_KEY_ID;
const RAZORPAY_KEY_SECRET = process.env.RAZORPAY_KEY_SECRET;

export async function POST(req: NextRequest) {
    try {
        // 1. Verify Firebase Authentication Token
        const authorization = req.headers.get('Authorization');
        if (!authorization?.startsWith('Bearer ')) {
            return NextResponse.json({ error: 'Unauthorized: Missing Authorization header' }, { status: 401 });
        }
        const idToken = authorization.split('Bearer ')[1];
        const decodedToken = await admin.auth().verifyIdToken(idToken);
        const userId = decodedToken.uid;

        // 2. Fetch user profile from Firestore
        const userDocRef = firestore.collection('users').doc(userId);
        const userDoc = await userDocRef.get();
        if (!userDoc.exists) {
            return NextResponse.json({ error: 'User not found in Firestore' }, { status: 404 });
        }
        const userData = userDoc.data();
        const userName = `${userData?.firstName || ''} ${userData?.surname || ''}`.trim() || 'User';
        const userEmail = userData?.email;

        if (!RAZORPAY_KEY_ID || !RAZORPAY_KEY_SECRET) {
            console.error('Razorpay API keys are not configured on the server.');
            return NextResponse.json({ error: 'Server configuration error: Missing API keys.' }, { status: 500 });
        }

        // 3. Create Razorpay Customer
        const customerDetails = {
            name: userName,
            email: userEmail,
            contact: userData?.mobileNo || '',
            fail_existing: '0',
        };

        const response = await fetch('https://api.razorpay.com/v1/customers', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Basic ${Buffer.from(`${RAZORPAY_KEY_ID}:${RAZORPAY_KEY_SECRET}`).toString('base64')}`,
            },
            body: JSON.stringify(customerDetails),
        });

        if (!response.ok) {
            const errorBody = await response.json();
            console.error('Razorpay API Error:', errorBody);
            throw new Error(errorBody.error.description || 'Failed to create Razorpay customer');
        }

        const customer = await response.json();
        
        // 4. Store customer ID in Firestore
        await userDocRef.update({
            razorpayCustomerId: customer.id,
        });

        // 5. Return the customer ID to the client
        return NextResponse.json({ customerId: customer.id });

    } catch (error: any) {
        console.error('Error creating Razorpay customer:', error);
        return NextResponse.json({ error: error.message }, { status: 500 });
    }
}
